							this.sheng=data.data[0]
							this.shi=data.data[1]
						this.dizhi=this.sheng+"-"+this.shi
							console.log(data.data.join(''))
						},
			save() {

				if (this.zhuanchang == '请选择') {
					uni.showToast({
						title: '请选择专长',
						duration: 2000,
						icon: "none"
